/* Reset some default styling */
body, ul {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-image: url('/src/background.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
    font-family: Archivo, sans-serif;
    overflow-x: hidden;
}

/* Style the header */
header {
    position: relative;
    z-index: 2; /* Set a higher z-index value for the header */
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    font-family: 'Archivo', serif;
    color: #598392;
    margin-bottom: 0px;
}

/* Add this rule to create a horizontal line */
.horizontal-line {
    border: none; /* Remove default border */
    height: 2px; /* Set the desired height for the line */
    background-color: #598392; /* Set the line color */
    /* Adjust the margin as needed to create space */
    width: 85%;
    margin: 10px auto 50px;
}

.image-collage {
    width: 85%;
    height: 50%;
    border-radius: 50px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.core-service {
    /* Set each service to occupy 50% of the available width (with spacing) */
    background-color: #598392; /* Choose your desired background color */
    color: #000000; /* Set the text color for better visibility */
    padding: 20px; /* Add padding for spacing */
    font-family: 'Archivo', sans-serif;
    font-size: 24px; /* Adjust font size as needed */
    margin-left: auto; /* Add margin to move it towards the right */
}

/* Style for the services section */
#services {
    display: grid;
    grid-template-columns: repeat(3, 3fr); /* Create three equal columns */
    gap: 20px; /* Add some gap between services */
    padding: 20px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
    background-color: #598392; /* Set the line color */
}

/* Style for individual service items */
.service {
    background-color: #598392; /* Make the service background transparent */
    color: #000000;
    padding: 20px;
    font-family: 'Archivo', sans-serif;
    text-align: -moz-left; /* Center align service text */
}

/* Style for the contact form section */
#contact {
    background-color: #598392; /* Make the service background transparent */
    padding: 20px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
    text-align: center;
}

#contact h2 {
    font-size: 36px;
    color: #000000; /* Set a different color for h3 headings */
}

#contact p {
    color: #FFFFFF; /* Set a different color for h3 headings */
}

#contact form input,
#contact form textarea {
    width: calc(100% - 20px);
    padding: 10px;
    margin: 5px 0 20px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
}

#contact form input[type="submit"] {
    cursor: pointer;
}

/* Style for the footer */
footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
    color:white
}

.social-icons img {
    width: 24px;
    height: auto;
    margin: 0 5px;
}

.contact-us-popup-container {
    position: relative;
    text-align: center;
}

.contact-us-popup {
    position: fixed;
    top: 50%; /* Vertically center the div */
    left: 50%; /* Horizontally center the div */
    transform: translate(-50%, -50%); /* Center the div precisely */
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 20px;
    width: 80%; /* Set the width as desired */
    max-width: 600px; /* Set a maximum width if needed */
    text-align: center;
    z-index: 1000;
}

.form-group {
    display: flex;
    gap: 10px;
}

.form-group input {
    flex: 1; /* Allow input fields to grow equally in width */
}

.close-popup {
    position: fixed;
    top: 10px;
    right: 10px;
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.close-popup:hover {
    color: red; /* Change the color to your preference */
}

.contact-us-button {
    background-color: #ffffff;
    color: black;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
}

.error-message {
    color: red;
    font-size: 12px;
}

.success-message {
    color: green;
    font-size: 12px;
    margin-top: 5px;
}

.contact-us-button:hover {
    background-color: #0056b3; /* Darker shade on hover */
}
/* ... (existing styles) */

.form-group button {
    margin-top: 10px; /* Add some spacing between buttons */
}

.primary-button {
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    margin-right: 10px; /* Add some spacing between buttons */
}

.primary-button:hover {
    background-color: #0056b3;
}

/* Media query for responsiveness */
@media screen and (max-width: 600px) {
    header {
        flex-direction: column;
        align-items: flex-start;
    }

    .header-text h1, .header-text p {
        margin: 0;
    }

    .header-text h1 {
        font-size: 40px;
    }

    .header-text p {
        font-size: 20px;
    }

    /* Style for the h3 headings inside the .service class */
    .service h3 {
        color: #000000; /* Set a different color for h3 headings */
        font-size: 24px;
    }

    /* Style for the p paragraphs inside the .service class */
    .service p {
        font-size: 16px;
        color: #FFFFFF; /* Set a different color for p paragraphs */
    }

    #services {
        grid-template-columns: 1fr; /* Change to a single column layout for small screens */
    }

    .logo {
        max-width: 300px;
        height: auto;
    }

    .second-div {
        margin-top: 10px; /* Add a margin at the top to create space */
    }
}